
// title
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import VXETable from '@/components/common/Table';
import NetWorkDetail from '@/views/main/NetWorkDetail';
import {
  listGateways,
  insertGateway,
  updateGatewayPwd,
  getList
} from '@/api/publishConfig.js';
export default {
  data() {
    return {
      tableLoading: false,
      dialogVisible: false,
      detailParam: {},
      selectRow: null,
      showEdit: false,
      islist: true,
      // 
      formData: {
        gatewayType: '',
        companyName: ''
      },
      // 
      companyList: [],
      // 
      NameList: [
        { label: '', value: '0' },
        { label: '', value: '1' }
      ],
      formTable: {
        companyCode: '',
        gatewayCode: '',
        gatewayName: '',
        gatewayType: '',
        companyName: ''

      },
      // 
      form2: {
        NewPassWord: '',
        RePassWord: ''
      },
      // 
      // rolerules1: {
      //   moduleName: [
      //     { required: true, message: '', trigger: 'blur' }
      //   ],
      //   description: [
      //     { required: true, message: '', trigger: 'blur' }
      //   ]
      // },
      // 
      formLabelWidth: '100px',
      // 
      tableData: [
        { gatewayName: '11' }
      ],
      // 
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        pageSizes: [5, 10, 20, 50, 100, 200, 500],
        layouts: ['PrevJump', 'PrevPage', 'Number', 'NextPage', 'NextJump', 'Sizes', 'Total'],
        perfect: true,
        slots: {
          // 
          left: 'pager_left'
        }
      }
    };
  },
  components: {
    HeaderTitle,
    HeadOperationItem,
    VXETable,
    NetWorkDetail
  },
  filters: {
    // 
    statusStyle(data) {
      switch (data) {
        case true :return 'primary';
        case false : return 'info';
        default: return data;
      }
    },

    // 
    statusContent(data) {
      switch (data) {
        case true : return '';
        case false : return '';
        default: return data;
      }
    },

    // 
    timeSwitch(value) {
      const date = new Date(value);
      const y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? ('0' + MM) : MM;
      let d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      let h = date.getHours();
      h = h < 10 ? ('0' + h) : h;
      let m = date.getMinutes();
      m = m < 10 ? ('0' + m) : m;
      let s = date.getSeconds();
      s = s < 10 ? ('0' + s) : s;
      return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
    }
  },
  methods: {
    // 
    handelVisit({ id, gatewayCode }) {
      this.detailParam = {
        gatewayId: id,
        gatewayCode
      };
      this.islist = false;
    },
    // 
    handleBack() {
      this.islist = true;
    },
    // 
    handleReload() {

    },
    handlePageChange1({ currentPage, pageSize }) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.listData();
    },
    // 
    listData() {
      this.tableLoading = true;
      const conditionJson = {
        pageNum: this.tablePage.currentPage,
        pageSize: this.tablePage.pageSize
      };
      listGateways(conditionJson).then(res => {
        this.tableLoading = false;
        // this.tableData = res.data;
        this.tablePage.total = res.data.length;
        this.tableData = res.data.slice((this.tablePage.currentPage - 1) * this.tablePage.pageSize, this.tablePage.currentPage * this.tablePage.pageSize);
      });
    },
    // 
    // onTablePageChange(currentPage, pageSize) {
    //   this.tablePage.currentPage = currentPage;
    //   this.tablePage.pageSize = pageSize;
    //   this.listData();
    // },
    // 
    closeit() {
      this.$refs.roleForm.resetFields();
    },
    // 
    modifyPassword(row) {
      this.dialogVisible = true;
      this.selectRow = row;
    },
    // 
    insertEvent() {
      this.showEdit = true;
    },
    // 
    PasswordConfirm(row) {
      const conditionJson = {
        id: this.selectRow.id,
        pwd: this.form2.NewPassWord
      };
      updateGatewayPwd(conditionJson).then(res => {
        if (res.code === 0) {
          this.dialogVisible = false;
          this.$message({
            message: '',
            type: 'success'
          });
        }
      });
    },
    // 
    submitEvent() {
      this.tableLoading = true;
      const conditionJson = {
        companyCode: this.formTable.companyCode,
        gatewayName: this.formTable.gatewayCode,
        companyName: this.formTable.companyName,
        gatewayType: this.formTable.gatewayName,
        // 
        gatewayCaptcha: 295865
      };
      insertGateway(conditionJson).then(res => {
        if (res.code === 0) {
          this.$message({
            message: '',
            type: 'success'
          });
          this.$refs.xTable.insert(this.formTable);
          this.tableLoading = false;
        }
      });
    },
    handlePageChange({ currentPage, pageSize }) {
      this.tablePage.currentPage = currentPage;
      this.tablePage.pageSize = pageSize;
      this.listData();
    },
    async findCompanyList() {
      try {
        this.tableLoading = true;
        const res = await getList(
          {
            size: this.tablePage.pageSize,
            current: this.tablePage.currentPage
          },
          {
            conditionJson1: [{
              column: 'state', // 
              operator: 1, // 
              ptype: 'string', // 
              type: 'in', // 
              value: '1,3'// 
            }]
          }
        );
        if (res.code === '0000') {
          // table
          this.tablePage.total = res.total;
          // 
          this.tableLoading = false;
          this.companyList = res.rows;
          console.log(res.rows);
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    }
  },
  created() {
    this.listData();
    this.findCompanyList();
  }
};
